.App {
  display: block;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.gui-element{
  outline-style: solid;
  margin: 1.5rem;
  padding: 1.5rem;
}

.gui-element>legend{
  position: absolute;
  margin-top: -2.5rem;
  margin-left: 0;
  padding: 0 0.5rem;
  background-color: white;
}

.menu{
  text-align: center;
}

.grammar-info{
  overflow-y: scroll;
  max-height: 270px;
}

.lba-info{
  overflow-y: scroll;
  max-height: 270px;
}

.LBA{
  overflow-x: scroll;
  overflow-y: scroll;
  max-height: 1080px;
}

.input > * {
  margin: 5px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
